.roadmap {
  padding: 24px 24px 48px;
  background: #08083b;

  .pre {
    height: 100vh;
    background: rgba(255, 255, 255, 0.25);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;


    .title {
      font-size: 100px;
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.05);
    }

    .label {
      font-size: 48px;
      color: rgba(255, 255, 255, 0.05);
    }

    .arrow {
      width: 50px;
      height: 50px;
      border-right: 10px solid rgba(255, 255, 255, 0.05);
      border-bottom: 10px solid rgba(255, 255, 255, 0.05);
      transform: rotate(45deg);
    }
  }

  .after {
    margin-top: 96px;
    height: 100vh;
    background: rgba(255, 255, 255, 0.25);
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 100px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.05);
  }

  .items {
    position: relative;
    margin-top: 96px;
    width: 2px;
    margin-left: auto;
    margin-right: auto;

    .light {
      position: absolute;
      z-index: 2;
      top: 0;
      left: calc(50% - 0.5px);
      transform: translate(-50%, -50%);
      width: 8px;
      height: 8px;
      background: lime;
      border-radius: 50%;
      box-shadow: 0 0 10px 3px lime;
    }

    .item {
      position: relative;

      .wrapper {
        position: relative;
        z-index: 3;
        width: 0;
        height: 0;

        .iconWrapper {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 32px;
          height: 32px;
          border-radius: 50%;
          background: #333;
          border: 3px solid #444;
        }

        .step {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          color: #555;
          font-size: 24px;
          font-weight: bold;
        }

        svg {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) scale(0);
          width: 24px;
          height: 24px;
          fill: red; //lime;
        }

        .label {
          position: absolute;
          top: 50%;
          left: 180px;
          transform: translateY(-50%);
          font-size: 40px;
          color: #333;
          white-space: nowrap;
          opacity: 0;
        }
      }

      .lineWrapper {
        position: relative;
        z-index: 1;
        left: -2px;
        width: 4px;
        height: 400px;
        background: #333;

        .line {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: lime;
          transform-origin: top;
          transform: scaleY(0);
        }
      }

    }


  }
}
